.block{
	
	display: flex;
	justify-content: space-between;
	max-width: 500px;
	min-width: 330px;
	height: auto;
	font-weight: 700;
	color: white;
	font-size: 30px;
	margin-top: 10vh;
	letter-spacing: 10px;
	line-height: 1;
	text-shadow: 0px 15px 5px rgba(0,0,0,0.1),
				10px 20px 5px rgba(0,0,0,0.05),
				-10px 20px 5px rgba(0,0,0,0.05);

}
.title{
	text-shadow: 4px 4px rgb(23, 27, 149);
}
.btn{
	margin-top: -2px;
	padding: 2px;
	max-height: 26px;
	max-width: 26px;
	background-color: rgba(grey, 0);
	border: 0px;
}
.exitBtn{
	color: white;
	cursor: pointer;
	border: 0;
	padding: 5px 10px;
	border-radius: 10px;
	background-color: rgba(58, 58, 58, 0.66);
	position:absolute;
	 top:10vh; 
	 right:5vh;
}
@media (max-width:545px) {
	.block{
		position: relative;
	}
	.exitBtn{
		position: absolute;
		right: -10px;
		top: -5vh;
	}
}